<template>
  <div id="wrapper" ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
import IScroll from 'iscroll/build/iscroll-probe'
import { mapGetters } from 'vuex'
export default {
  name: 'ScrollVue',
  data: function () {
    return {
      iscroll: null,
      routerPath: ''
    }
  },
  props: {
    probeType: {
      type: Number,
      default: 1,
      require: true
    },
    time: {
      type: Number,
      default: 200,
      require: true
    }
  },
  methods: {
    // 
    scrolling (fn) {
      this.iscroll.on('scroll', function () {
        fn(this.y)
      })
    },
    refresh () {
      setTimeout(() => {
        this.iscroll.refresh()
      }, 200)
    },
    scrollTo (x, y, time) {
      this.iscroll.scrollTo(x, y, time)
    },
    currentY () {
      return this.iscroll.y
    },
    maxScrollY () {
      return this.iscroll.maxScrollY
    },
    scrollStart (fn) {
      this.iscroll.on('scrollStart', function () {
        fn()
      })
    },
    scrollEnd (fn) {
      this.iscroll.on('scrollEnd', function () {
        fn()
      })
    },
    destroy () {
      this.iscroll = null
    }
  },
  mounted () {
    setTimeout(() => {
      this.iscroll = new IScroll(this.$refs.wrapper, {
        mouseWheel: true,
        scrollbars: false,
        // 
        probeType: this.probeType,
        // 
        // 
        bounce: false,
        // bounceEasing: 'quadratic',
        // 
        deceleration: 0.002,
        // 
        // bounceTime: 200,
        scrollX: false,
        scrollY: true,
        disableTouch: false,
        disableMouse: true,
        hScrollbar: false,
        vScrollbar: false,
        // click
        preventDefault: false
      })
      // 1 
      /*
        * MutationObserver
        * mutationList
        * observer
        * */
      var observer = new MutationObserver((mutationList, observer) => {
        this.iscroll.refresh()
      })
      // 2 
      const config = {
        // attributes: true, // 
        childList: true, // 
        subtree: true, //  false
        attributeFilter: ['height', 'offsetHeight'] // 
        // characterData: true // 
        // attributeOldValue: true, //  attributes 
        // characterDataOldValue: true //  characterData 
      }
      // 3 
      /*
        * 1
        * 2
        * */
      observer.observe(this.$refs.wrapper, config)
    }, 200)
  },
  computed: {
    ...mapGetters([
      'isShowMiniPlayer'
    ])
  },
  watch: {
    // miniPlayer /iScroll
    // wrapperbottom@include position_bottom;
    isShowMiniPlayer (newValue) {
      document.documentElement.setAttribute('data-mini', newValue)
      this.refresh()
    }
  },
  //  keep-alive 
  activated () {
    // 
    this.refresh()
  }
}
</script>

<style scoped lang="scss">
  #wrapper{
    width: 100%;
    height: 100%;
  }
</style>
